body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: calc(16px + .35vw);
  text-rendering: optimizeLegibility;
  font-weight: 500;
}

img {
  max-width: 100%;
  position: relative;
  z-index: 1;
}

body {
  cursor: url('./img/cursorimg.png'), auto!important;
}

h1, h2, h3, h4, h5, h6,
.ant-menu-item, 
.ant-menu-submenu-title {
  text-transform: uppercase;
  margin-top: 0!important;
  line-height: 1.2!important;
}

h4.ant-typography, .ant-typography h4 {
  text-transform: uppercase;
  font-size: 14px;
}

h3.ant-typography, .ant-typography h3,
h2.ant-typography, .ant-typography h2 {
  margin-bottom: 1em;
  font-size: calc(1em + .5vw);
}

blockquote {
  padding: 1em;
  margin: 1em 0;
  border-left: 1px solid #e8e8e8;
  font-size: 14px;
}

.herotext {
  &:before,
  &:after {
    content: "";
    background: -moz-linear-gradient(top,  rgba(255,255,255,0) 0%, rgba(255,255,255,1) 50%);
    background: -webkit-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 50%);
    background: linear-gradient(to bottom,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 50%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=0 );
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
  &:after {
    background: -moz-linear-gradient(left,  rgba(255,255,255,0) 0%, rgba(255,255,255,1) 80%);
    background: -webkit-linear-gradient(left,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 80%);
    background: linear-gradient(to right,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 80%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=1 );
  }

  .ant-btn-primary {
    float: left;
    clear: both;
    margin-bottom: 3em;
  }
}

#herobg,
#hero-u2 {
  height: 100vh;

  .heroparagraph {
    max-width: 670px;
  }

  canvas {
    opacity: .9;
  }
}

#hero-u2 {
  .herotext {
    &:before,
    &:after {
      display: none;
    }
  }
}

#root {
  position: relative;
  z-index: 1;
}

.ant-layout-header {
  background: #fff;
  position: fixed;
  width: 60vw;
  left: 0;
  right: 0;
  margin: auto;
}

.mrlogo {
  background: url('./img/mobilerider-logo-dark.svg') no-repeat;
  max-width: 140px;
  height: 15px;
  margin: 4em 0 2em;
  display: block;
  width: 100%;
}

.ant-layout-sider {
  background: none;
  height: 100vh;
  z-index: 2;
  position: fixed;
  right: 0;
  top: 0;
  -moz-transition: .3s;
  -ms-transition: .3s;
  -o-transition: .3s;
  -webkit-transition: .3s;
  transition: .3s;

  .lateststreams {
    position: absolute;
    bottom: 2vh;
    font-size: 9px;
    text-transform: uppercase;
    max-width: 280px;
    left: 0;
    max-height: 350px;
  }
}

.social {
  position: fixed;
  right: 0;
  font-size: 9px;
  text-transform: uppercase;
  font-weight: bold;
  top: 32vh;
  

  a {
    margin-bottom: 8em;
    color: inherit;
    display: block;
    -webkit-transform: rotate(-90deg);
    -moz-transform: rotate(-90deg);
    -ms-transform: rotate(-90deg);
    -o-transform: rotate(-90deg);
    filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=3);
  }
}

.ant-typography,
.ant-layout-footer {
  position: relative;
  z-index: 2;

  a {
    text-transform: uppercase;
    font-weight: bold;
    border-bottom: 1px solid;
    font-size: 11px;
    line-height: 22px;
    display: inline-block;
  }
}

.ant-layout,
.ant-menu {
  background: inherit;
}

h1.ant-typography, 
.ant-typography h1 {
  font-size: calc(2em + 1vw);
}

h2.ant-typography, 
.ant-typography h2 {
  font-size: calc(1em + 1vw);
  white-space: nowrap;
}

.ant-menu-inline, .ant-menu-vertical, .ant-menu-vertical-left {
  border-left: 1px solid #e8e8e8;
  border-right: none;

  .ant-menu-item {
    height: 25px;
    line-height: 25px;
    font-size: 12px;

    a {
      display: block;
      position: relative;
      color: rgba(0, 0, 0, 0.65);

      &:hover {
        color: #d2812d;
      }
    }
  }
}

.ant-menu-item-selected a, 
.ant-menu-item-selected a:hover {
  color: #d2812d!important;
}

.ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background-color: inherit;
}

.ant-card {
  box-shadow: 0 14px 38px rgba(24, 144, 255, 0.1);

  .ant-btn {
    margin: auto;
    display: block;
    border: none;
    border-bottom: 1px solid;
    text-transform: uppercase;
    border-radius: 0;
    padding: 0;
    font-size: 11px;
    font-weight: bold;
  }

  .ant-card-body {
    padding-top: 16px;
  }

  .ant-card-meta-title {
    font-size: calc(16px + .25vw);
  }
  
  .ant-card-meta-description {
    font-size: 16px;
  }

  .ant-card-head-title {
    color: #d2812d;
  }
}

div.ant-typography, 
.ant-typography p {
  margin-bottom: 1.5em;
}

.ant-btn {
  border: 1px solid #1890ff;
  position: relative;
  z-index: 2;

  &.ant-btn-primary {
    box-shadow: 0 14px 38px rgba(24, 144, 255, 0.3);
  }
}

.partners {
  background: url('./img/partners.png') no-repeat;
  height: 62px;
  background-size: contain;
  position: relative;
  z-index: 1;
}

#services,
#platform {
  padding: 10vh 0;
  z-index: 2;

  .ant-typography {
    font-size: calc(16px + .35vw);
    margin-top: 0;

    h2 {
      -webkit-transform: rotate(-90deg);
      -moz-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
      -o-transform: rotate(-90deg);
      filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=3);
      white-space: nowrap;
      font-size: calc(1em + 1vw);
    }

    h4 {
      font-size: 12px;
    }

    ul {
      li {
        float: left;
        padding: 5%;
        width: 40%;
        line-height: 1.3;
        border-radius: 25px;
        margin: .5em;
        background: rgba(255, 255, 255, 0.5);
        font-size: 16px;

        .anticon {
          position: absolute;
          left: 0;
          right: 0;
          margin: auto;
          font-size: 24px;
        }

        p {
          padding: 1.5em 0;
          display: table-cell;
          vertical-align: middle;
        }
      }
    }
    .ant-card {
      border: none;
      background: none;
      box-shadow: none;

      .ant-card-grid {
        box-shadow: inherit;
        width: 100%;

        .anticon {
          font-size: 24px;
          margin: 1em 0 .5em;
          color: #d2812d;
        }
      }
    }
  }
}

#services {
  .ant-typography {
    max-width: 500px;

    h2 {
      -webkit-transform: rotate(90deg);
      -moz-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
      -o-transform: rotate(90deg);
      filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=3);
    }
  }
}

.servicesimg,
.servicesimg2,
.servicesimg3,
.platformimg1,
.platformimg2,
.platformimg3,
.platformimg4 {
  max-width: 100%;
  background-size: contain!important;
  position: relative;
  z-index: 1;
  margin-bottom: 2em;
  border-radius: 25px;
}

.servicesimg {
  background: url('./img/production.jpg') no-repeat;
}
.servicesimg2 {
  background: url('./img/audi.jpg') no-repeat;
}
.servicesimg3 {
  background: url('./img/sat-van.jpg') no-repeat;
}
.platformimg1 {
  background: url('./img/screens.png') no-repeat;
  float: left;
  height: 1000px;
  width: 1000px;
}
.servicesimg,
.servicesimg2,
.servicesimg3 {
  width: 500px;
  height: 375px;
  background-size: inherit!important;
}

.login {
  position: fixed;
  top: 2em;
  right: 2em;
  font-size: 12px;
}

.line {
  stroke: rgba(0, 0, 0, 0.85);
  stroke-width: 1;
}

.ant-divider {
  position: relative;
  z-index: 2;
  margin: 4em 0;
}

.ant-drawer-content-wrapper {
  max-width: 1200px;

  .ant-drawer-header,
  .ant-drawer-body,
  .featuredlogo {
    padding: 2em 4em;
  }

  .ant-drawer-header {
    background: inherit;
    padding: 2em;
  }

  img {
    display: block;
    margin: 0 auto 2em;
  }

  .ant-drawer-body {
    font-size: calc(16px + .1vw);

    img {
      margin: 1em 0 2em;
    }
  }
}

.ant-drawer-title {
  text-transform: uppercase;
  font-size: inherit;
}

.featuredlogo {
  max-width: 300px;
}

.ant-tabs {
  position: relative;
  z-index: 2;

  .ant-tabs-bar {
    text-transform: uppercase;
  }

  .ant-tabs-nav {
    color: #1890ff;
    
    .ant-tabs-tab-active {
      color: #d2812d;
    }
  }

  .ant-tabs-tabpane {
    padding: 4vh 0 0;
  }

  .ant-tabs-nav-container {
    font-size: 12px;
  }

  .ant-tabs-content {
    min-height: 1000px;
  }
}

.ant-layout-footer {
  background-color: rgba(32, 56, 70, .95);
  color: #6a8798;
  font-size: 12px;
  padding-bottom: 5vh;

  img {
    max-width: 140px;
    display: block;
    margin: 2em auto;
  }

  .ant-divider {
    opacity: .1;
  }

  h4 {
    color: white;
    font-size: 12px;
  }

  a {
    border: none;
    display: block;
    font-size: 10px;
  }

  .ant-col {
    text-align: left;
  }

  .copyright {
    text-align: center;
  }
}

#ready {
  min-height: 100vh;
  position: relative;
  z-index: 2;
  background: white;
  padding: 20vh 0;

  h2 {
    padding-bottom: 1em;
  }

  .ant-carousel .slick-slide {
    min-height: 13vh;
    overflow: hidden;
  }
  .ant-carousel .slick-dots li button,
  .ant-carousel .slick-dots li.slick-active button {
    background: #d2812d;
  }

  blockquote {
    font-size: calc(16px + .15vw);
  }

  small {
    display: block;
    padding: 2em 0;
    font-style: italic;
    color: rgba(0, 0, 0, 0.65);
    font-size: small;
  }
}

form {
  input,
  textarea {
    border: 1px solid #e8e8e8;
    margin: .5em 0;
    padding: 1em;
    width: 100%;
    background: none;
  }
  button, html [type="button"], [type="reset"], [type="submit"] {
    cursor: pointer;
    width: inherit;
    box-shadow: inherit;
    border: 1px solid #1890ff;
    color: #1890ff;
    border-radius: 5px;
    background: none;
  }
}

.phish,
.rhcp,
.facebook,
.nasa,
.grammys,
.adobe,
.u2,
.f8 {
  max-width: 100%;
  float: left;
  width: 50%;
  background-position: center top !important;
  min-height: 700px;
  background-size: cover !important;
  position: relative;
  z-index: 3;
  -moz-transition: .3s;
  -ms-transition: .3s;
  -o-transition: .3s;
  -webkit-transition: .3s;
  transition: .3s;
  border-radius: 25px;
  box-shadow: 0 14px 38px rgba(24, 144, 255, 0.3);
  overflow: hidden;

  .ant-typography {
    color: white;
    padding: 2%;
    position: relative;
  }

  .ant-descriptions {
    background: -moz-linear-gradient(top,  rgba(255,255,255,0) 0%, rgba(0,0,0,1) 100%);
    background: -webkit-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(0,0,0,1) 100%);
    background: linear-gradient(to bottom,  rgba(255,255,255,0) 0%,rgba(0,0,0,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#000000',GradientType=0 );
    position: absolute;
    bottom: 0;
    padding: .5em;

    .ant-descriptions-title,
    .ant-descriptions-item-label,
    .ant-descriptions-item-content {
      color: white;
    }

    .ant-descriptions-title {
      text-transform: uppercase;
    }

    .ant-descriptions-item-label {
      font-weight: bold;
    }

    .ant-descriptions-row {
      td {
        padding: 0 1em 1em;

        span {
          max-width: 600px;
        }
      }
    }

    ul {
      font-size: small;
    }
  }
}

.phish {
  background: url('./img/phish.jpg') no-repeat;
}

.facebook {
  background: url('./img/hero-pope.jpg') no-repeat;
}

.rhcp {
  background: url('./img/rhcp.jpg') no-repeat;
}

.nasa {
  background: url('./img/featured-nasa.jpg') no-repeat;
}

.grammys {
  background: url('./img/featured-grammys.jpg') no-repeat;
}

.adobe {
  background: url('./img/adobemax.jpg') no-repeat;
}

.u2 {
  background: url('./img/u2.jpg') no-repeat;
}

.f8 {
  background: url('./img/f8.jpg') no-repeat;
}

// .kaboo {
//   background: url('./img/kaboo.jpg') no-repeat;
// }

.ant-layout-sider-light .ant-layout-sider-zero-width-trigger {
  right: 1em;
  top: 1em;
  background: none;
}

.ant-layout-sider-light .ant-layout-sider-zero-width-trigger {
  svg {
    width: 1.5em;
    height: 1.5em;
    right: 1em;
    top: 1em;
  }
}

.ant-layout-sider-light .ant-layout-sider-zero-width-trigger {
  font-size: 14px;
  line-height: 47px;
  color: #258bad;
}

.featuredshots {
  padding: 0 0 4vh;
  background: #f6f6f6;

  .u2-1,
  .u2-2,
  .u2-3,
  .slc {
    height: 0;
    padding-bottom: 100%;
    width: 100%;
    margin-top: -14vh;
    max-width: 1300px;
    background-size: contain;
    margin-bottom: 4vh;
  }
}

.u2-1 {
  background: url('./img/u2-1.jpg') no-repeat;
}

.u2-2 {
  background: url('./img/u2-2.jpg') no-repeat;
}

.u2-3 {
  background: url('./img/u2-3.jpg') no-repeat;
}

.slc {
  background: url('./img/slc.jpg') no-repeat;
  height: 326px!important;
  padding-bottom: inherit!important;
  background-size: cover!important;
}

/* Animation */

@keyframes fadeInUp {
    from {
        transform: translate3d(0,40px,0)
    }

    to {
        transform: translate3d(0,0,0);
        opacity: 1
    }
}

@-webkit-keyframes fadeInUp {
    from {
        transform: translate3d(0,40px,0)
    }

    to {
        transform: translate3d(0,0,0);
        opacity: 1
    }
}

.animated {
    animation-duration: 1s;
    animation-fill-mode: both;
    -webkit-animation-duration: 1s;
    -webkit-animation-fill-mode: both
}

.animatedFadeInUp {
    opacity: 0
}

.fadeInUp {
    opacity: 0;
    animation-name: fadeInUp;
    -webkit-animation-name: fadeInUp;
}


@media only screen and (max-width: 1600px) {
  .lateststreams {
    max-width: 200px;
    height: 150px;
    overflow: hidden;
  }
  body {
    font-size: 16px;
  }
  .ant-menu-inline .ant-menu-item, .ant-menu-vertical .ant-menu-item, .ant-menu-vertical-left .ant-menu-item {
    font-size: 11px;
    height: 22px;
    line-height: 22px;
  }
  #featuredwork .ant-col div,
  #featuredwork .ant-col-17 {
    width: 100%;
  }
}

@media only screen and (max-width: 1200px) {
  .mrlogo {
    position: fixed;
    right: 4em;
    margin: 0;
    top: 30px;
  }
  
  .login,
  #services .ant-typography h2, #platform .ant-typography h2 {
    display: none;
  }

  .servicesimg, .servicesimg2, .servicesimg3, .platformimg1, .platformimg2, .platformimg3, .platformimg4 {
    max-height: 350px;
  }

  .ant-layout-sider {
    background: white;
    padding: 5em 0;
  }

  .ant-menu-inline, .ant-menu-vertical, .ant-menu-vertical-left {
    border: none;
    background: white;
    z-index: 1;
    position: relative;
    padding: 1em;
  }

  h1.ant-typography, .ant-typography h1 {
    font-size: calc(1em + 1.5vw);
  }

  #services .ant-col-6 {
    width: 35%;
  }

  .u2, .f8, .adobe, .phish, .rhcp, .facebook, .nasa, .grammys {
    min-height: 360px;
  }
}

@media only screen and (max-width: 767px) {
  body,
  #services .ant-typography, #platform .ant-typography,
  .ant-card,
  q {
    font-size: 12px;
  }

  h3.ant-typography, .ant-typography h3 {
    font-size: calc(16px + .5vw)!important;
  }
  
  .social,
  .herotext h4,
  .mobilehide,
  #featuredwork table tr:nth-child(3),
  #featuredwork .ant-descriptions-row:last-child {
    display: none;
  }

  .mrlogo {
    max-width: 110px;
    right: 6em;
  }
  
  h1.ant-typography, .ant-typography h1 {
    font-size: 20px;
  }
  
  .herotext {
    width: 91%;
    padding-top: 10vh;
  }

  .ant-tabs {
    .ant-tabs-nav-container {
      font-size: 9px;
    }

    .ant-tabs-left-bar {
      border-right: none;
      width: 100%;
    }

    .ant-tabs-left-content {
      position: absolute;
      top: 530px;
      width: 100%;
      padding-left: 0;
    }

    .ant-tabs-content {
      min-height: inherit;
    }
  }

  #services .ant-col,
  #platform .ant-col,
  #ready .ant-col,
  .ant-layout-footer .ant-col {
    width: 100%;
    padding: 0 6%;
  }

  #ready .ant-col {
    width: 100%;
  }

  .ant-layout-footer .ant-col {
    padding-bottom: 3vh;
  }

  .ant-layout-footer .copyright {
    padding-bottom: inherit;
  }

  #ready {
    padding: 20vh 2em;

    form {
      padding-bottom: 4vh;

      div {
        transform: inherit!important;
      }
    }
  }

  .ant-carousel {
    line-height: 1.2;
  }

  .ant-card-grid div,
  #ready div {
    transform: inherit!important;
  }

  .ant-card-contain-grid {
    padding-bottom: 4vh;
  }

  h2.ant-typography, .ant-typography h2 {
    font-size: 30px;
  }

  .u2, .f8, .adobe, .phish, .rhcp, .facebook, .nasa, .grammys {
    min-height: 400px;

    .ant-descriptions-item-content {
      font-size: 12px;

      ul {
        font-size: 10px;
      }
    }
  }

  .servicesimg, .servicesimg2, .servicesimg3, .platformimg1, .platformimg2, .platformimg3, .platformimg4 {
    max-height: 300px;
    border-radius: 0;
  }
}